import React, { useState } from 'react'
import { Card, Table, Input, Modal, message, Button } from 'antd';
import { DeleteTwoTone, SearchOutlined, PlusCircleOutlined, UserAddOutlined, PushpinTwoTone } from '@ant-design/icons';
import Flex from 'components/shared-components/Flex';
import utils from 'utils';
import RoutineForm from './form';
import StudentForm from './studentForm';
import MarkBoardForm from './boardForm';
import { useMutation } from '@apollo/client';
import { DELETE_ROUTINE } from 'graphql/delete';
import IntlMessage from 'components/util-components/IntlMessage';

const RoutineTable = ({ permissions, data, loading, fetchRoutines, fetchTimes, refetch, refetchTimes }) => {

    const [filter, setFilter] = useState('');

	const [isModalVisible, setIsModalVisible] = useState(false);

	const [studentModalVisible, setStudentModalVisible] = useState(false);
	const [boardData, setBoardData] = useState([]);

	const [boardModalVisible, setBoardModalVisible] = useState(false);
	
	const [formType, setFormType] = useState("");

    const triggerFetch = (page, pageSize) => {
        var start = 0;
        var end = pageSize;
        if (page !== 1) {
            start = (page * pageSize) - pageSize
            end = pageSize * page
        }
        fetchRoutines({ variables: { offset: start, limit: (end - 1), filter: filter } })
        fetchTimes({ variables: { offset: start, limit: (end - 1), filter: filter } })
    }

    const onPageChange = (page, pageSize) => {
        triggerFetch(page, pageSize);
    }

    const showModal = () => {
		setFormType("create");
      	setIsModalVisible(true);
    };

	const showBoardModal = (row) => {
		setBoardModalVisible(true);
		setBoardData(row);
	}

    const handleCancel = () => {
      	setIsModalVisible(false);
		setStudentModalVisible(false);
		setBoardModalVisible(false);
	};
  
	const showStudentModal = (row) => {
		setStudentModalVisible(true);
		setBoardData(row);
	}

	const { confirm } = Modal;

	const [deleteRoutine, { loading: loadingDelete }] = useMutation(DELETE_ROUTINE, {
		onCompleted: data => {
            message.success(' ');
		}
	});
	
	function deleteRow(row) {
		confirm({
		  title: ' ?',
		  okText: '',
		  okType: 'danger',
		  cancelText: '',
		  onOk() {
			deleteRoutine({ variables: { id: row.id } }); 
		  },
		});
	}

	const tableColumns = [
		{
			key: 'schoolyear',
			title: <IntlMessage id="schoolyear" />,
			dataIndex: ['schoolyear', 'schoolyear'],
			sorter: (a, b) => utils.antdTableSorter(a, b, 'schoolyear')
		},
		{
			key: 'classes',
			title: <IntlMessage id="classes" />,
			dataIndex: ['classes', 'classes'],
			sorter: (a, b) => utils.antdTableSorter(a, b, 'classes')
		},
		{
			key: 'subject',
			title: <IntlMessage id="subject" />,
			dataIndex: ['subject', 'subject'],
			sorter: (a, b) => utils.antdTableSorter(a, b, 'subject')
		},
		{
			key: 'teacher',
			title: <IntlMessage id="teacher" />,
            children: [
                {
                    key: 'familyName',
                    title: <IntlMessage id="familyName" />,
                    dataIndex: ['teacher', 'familyName']
                },
                {
                    key: 'teacher',
                    title: <IntlMessage id="name" />,
                    dataIndex: ['teacher', 'name']
                }
            ],
			sorter: (a, b) => utils.antdTableSorter(a, b, 'teacher')
		},
	];
	if (permissions.edit === true || 
		permissions.destroy === true || 
		permissions.add_mark_board === true ||
		permissions.view_routine_student === true) {
		tableColumns.push(
			{
				key: 'actions',
				title: <IntlMessage id="main.action" />,
				width: '10wd',
				dataIndex: 'actions',
				render: (_, elm) => (
					<div className="text-center">
						
						{ permissions.add_mark_board === true &&
							<Button 
								type='text'
								onClick={() => showBoardModal(elm)}
							>
								<PushpinTwoTone twoToneColor="#eb2f96"/>
								<span className="ml-2"><IntlMessage id="mark-board" /></span>
							</Button>
						}
						{ permissions.view_routine_student === true &&
							<Button
								type='text'
								onClick={() => showStudentModal(elm)}
							>
								<UserAddOutlined twoToneColor="#eb2f96"/>
								<span className="ml-2"><IntlMessage id="student" /></span>
							</Button>
						}
						{ permissions.destroy === true &&
							<Button
								type='text'
								onClick={() => deleteRow(elm)}
							>
								<DeleteTwoTone twoToneColor="#f42f2f"/>
								<span className="ml-2"><IntlMessage id="delete" /></span>
							</Button>
						}
					</div>
				)
			}
		)
	}

	const onSearch = e => {
		const value = e.currentTarget.value;
        fetchRoutines({ variables: { offset: 0, limit: 10, filter: filter } })
        fetchTimes({ variables: { offset: 0, limit: 10, filter: filter } })
        setFilter(value)
	}

	return (
		<>
			<Modal 
                title={<IntlMessage id="add_new" />}
                visible={isModalVisible} 
				okText={<IntlMessage id="main.okText" />}
				cancelText={<IntlMessage id="main.cancelText" />}
				onCancel={handleCancel}
				width={'80vw'}
				okButtonProps={{form:'RoutineForm', key: 'submit', htmlType: 'submit'}}
            >
                <RoutineForm 
					formType={formType} 
					setIsModalVisible={setIsModalVisible}
                    refetch={refetch}
                    refetchTimes={refetchTimes}
				/>
            </Modal>
			<Modal 
                title={<IntlMessage id="add_student" />}
                visible={studentModalVisible} 
                footer={null}
				onCancel={handleCancel}
                width={'80vw'}
            >
                <StudentForm
					setStudentModalVisible={setStudentModalVisible}
					boardData={boardData}
				/>
            </Modal>
			<Modal 
				title={<IntlMessage id="mark-board" />}
				visible = {boardModalVisible}
				okText={<IntlMessage id="main.okText" />}
				cancelText={<IntlMessage id="main.cancelText" />}
				onCancel={handleCancel}
				okButtonProps = {
					{
						form: 'MarkBoardForm',
						key: 'submit',
						htmlType: 'submit'
					}
				}
			>
				<MarkBoardForm
					setBoardModalVisible={setBoardModalVisible}
					boardData={boardData}
				/>
			</Modal>
			<Card>
				<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
					<Flex className="mb-1" mobileFlex={false}>
						<div className="mr-md-3 mb-3">
							<Input placeholder={''} prefix={<SearchOutlined />} onChange={e => onSearch(e)}/>
						</div>
					</Flex>
					{ permissions.create === true && 
						<div className="mr-md-3 mb-3">
							<Button onClick={showModal} type="primary" icon={<PlusCircleOutlined />} block> <IntlMessage id="add_new" /></Button>
						</div>
					}
				</Flex>
				<div className="table-responsive">
					<Table 
                        loading={loading || loadingDelete}
						columns={tableColumns} 
						dataSource={data}
						rowKey='id' 
						bordered
					/>
				</div>
			</Card>
		</>
		
	)
}

export default RoutineTable
