import React, { useEffect, useState } from 'react';
import { Form, Input, message, Spin, Row, Col, Select, DatePicker, Empty } from 'antd';
import { useMutation, useQuery, useLazyQuery } from '@apollo/client';
import { CREATE_ROUTINE } from 'graphql/core';
import { ALL_PROGRAMS, ALL_SCHOOL_YEAR, ALL_SECTIONS } from 'graphql/all';
import { ALL_PROGRAM_SUBJECT_BY_PROGRAM } from "graphql/custom";
import moment from 'moment';
import IntlMessage from 'components/util-components/IntlMessage';
import Loading from 'components/shared-components/Loading';
import { SELECT_TEACHER, SELECT_CLASSES } from 'graphql/select';
import useDomain from 'hooks/useDomain';

function RoutineForm ({ setIsModalVisible, refetch, refetchTimes }) {

    const [form] = Form.useForm();
    const { Option } = Select;
    const { RangePicker } = DatePicker;
    const { domainData } = useDomain();

    const [times, setTimes] = useState([])

    const [createRoutine, { loading }] = useMutation(CREATE_ROUTINE, {
		onCompleted: data => {
            message.success(' ');
            form.resetFields();
            setIsModalVisible(false);
            refetch();
            refetchTimes();
		}
	});

    const [allClassess, { data: classessData }] = useLazyQuery(SELECT_CLASSES);
    const { data: programData } = useQuery(ALL_PROGRAMS);
    const { data: schoolYearData } = useQuery(ALL_SCHOOL_YEAR);
    const { data: sectionData } = useQuery(ALL_SECTIONS);
    const [allSubjects, { data: subjectData }] = useLazyQuery(ALL_PROGRAM_SUBJECT_BY_PROGRAM);

    const [teacherData, setTeacherData] = useState([])
    const [fetchTeacher, { loadint: teacherLoading }] = useLazyQuery(SELECT_TEACHER, {
        onCompleted: data => {
            setTeacherData(data.allTeachers);
        }
    });

    const onTeacherSearch = value => {
        if (value === '') {
            setTeacherData([]);
        } else {
            fetchTeacher({ variables: { offset: 0, limit: 99999999, filter: value } });
        }
    }

    const programOnChange = (value) => {
		allClassess({ variables: { program: value, offset: 0, limit: 0, filter: '' } });  
        allSubjects({ variables: { id: value } });
    }

    const [classes, setClasses] = useState("");

    const classesOnChange = (value) => {
        setClasses(value);
    }

    let classesType = null;
    
    let sectionOption = null;
    
    if (classes) {
        classesType = classes;
    }
    
    if (classesType) {
        sectionOption = sectionData?.allSections.filter(section => section.classes.id === classesType).map(filteredSection => (
            <Option key={filteredSection.id} value={filteredSection.id}>{filteredSection.section}</Option>
        ))
    }

    const [forDate, setForDate] = useState(null);

    const forDateOnChange = (value) => {
        setForDate(value);
    }

    if (forDate) {
        disabledDate();
    }

    function disabledDate(current) {
        var date = parseInt(forDate);
        return (
            moment(current).day() !== date
        )
    }

    const onFinish = values => {

        values.dates.map(function (date, index) {
            if (index === 0) {
                values.startDate = moment(date).format("YYYY-MM-DD")
            } else {
                values.endDate = moment(date).format("YYYY-MM-DD") 
            }
            return null
        })

        if (values.hasOwnProperty('endDate')) {
            // createRoutine({ variables: values})
        
            values.time.map(function(time) {
                const variables = values
                variables.time = time
                return (
                    createRoutine({ variables: variables})
                )
            })
        }
    };

    useEffect(() => {
        function loopTimes() {
            let maxTime = 0 
            if (domainData.hostname === 'tuvptk.edu.mn') {
                maxTime = 8
            } else {
                maxTime = 4
            }
            for (let index = 1; index <= maxTime; index++) {
                setTimes(prevState => [...prevState, {
                    value: index,
                    name: `1-  ${index}- `
                }])
            }
            for (let index = 1; index <= maxTime; index++) {
                setTimes(prevState => [...prevState, {
                    value: index+maxTime,
                    name: `2-  ${index}- `
                }])
            }
        }
        if (domainData) {
            loopTimes()
        }
    }, [domainData])
    

    return (
        <Spin spinning={loading} tip=" ...">
            <Form  
                id="RoutineForm"
                layout={'vertical'}
                form={form}
                name="control-hooks" 
                onFinish={onFinish}
            >
                <Row gutter={[16, 16]}>
                    <Col span={12}>
                        <Form.Item name="schoolyear" label={<IntlMessage id="schoolyear" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select>
                                { schoolYearData?.allSchoolyears.map((schoolyear, index) => (
                                    <Option key={index} value={schoolyear.id} >{schoolyear.schoolyear} {schoolyear.season}</Option>
                                ))}
                            </Select>
                        </Form.Item>
                        <Form.Item name="classes" label={<IntlMessage id="classes" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select onChange={classesOnChange}>
                                {
                                    classessData?.allClassess.map(classes => (
                                        <Option key={classes.id} value={classes.id}>{classes.classes} / {classes.classesNumeric}</Option>
                                    ))
                                }
                            </Select>
                        </Form.Item>
                        <Form.Item name="teacher" label={<IntlMessage id="teacher" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select
                                showSearch
                                filterOption={false}
                                notFoundContent={
                                    teacherLoading ? <Loading cover='content' /> 
                                    : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
                                }
                                onSearch={onTeacherSearch}
                            >
                                { teacherData.map((item, index) => (
                                    <Option value={item.id} key={index} > {item.familyName} {item.name} / {item.teacherCode} </Option>
                                ))}
                            </Select>
                        </Form.Item>
                        <Form.Item name="fordate" label={<IntlMessage id="class.day" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select onChange={forDateOnChange}>
                                <Option key={0} value="1" ></Option>
                                <Option key={1} value="2" ></Option>
                                <Option key={2} value="3" ></Option>
                                <Option key={3} value="4" ></Option>
                                <Option key={4} value="5" ></Option>
                            </Select>
                        </Form.Item>
                        <Form.Item 
                            name="time" 
                            label={<IntlMessage id="class.time" />} 
                            rules={[
                                { 
                                    required: true,
                                    message: "  "
                                }
                            ]}
                        >
                            <Select
                                mode="multiple"
                            >
                                { times.map((item, index) => (
                                    <Option key={index} value={item.value} >{item.name}</Option>
                                ))}
                            </Select>
                        </Form.Item>
                        <Form.Item name="weekly" label={<IntlMessage id="weekly" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select>
                                <Option key={0} value={0} ><IntlMessage id="weekly.all" /></Option>
                                <Option key={1} value={1} ><IntlMessage id="weekly.option" /></Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item name="program" label={<IntlMessage id="program" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select onChange={programOnChange}>
                                { programData?.allPrograms.map((program, index) => (
                                    <Option key={index} value={program.id} >{program.program}</Option>
                                ))}
                            </Select>
                        </Form.Item>
                        <Form.Item name="section" label={<IntlMessage id="section" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select>
                                {sectionOption}
                            </Select>
                        </Form.Item>
                        <Form.Item name="subject" label={<IntlMessage id="subject" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select>
                                { 
                                    subjectData?.allProgramSubjectByProgram.map((subject, index) => (
                                        <Option key={index} value={subject.subject.id}>{subject.subject.subject} / {parseFloat(subject.subject.credit).toString()}</Option>
                                    ))
                                }
                            </Select>
                        </Form.Item>
                        <Form.Item name="room" label={<IntlMessage id="class.room" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Input />
                        </Form.Item>
                        <Form.Item name="dates" label={<IntlMessage id="start_end_date" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <RangePicker
                                disabledDate={disabledDate}
                            />
                        </Form.Item>
                        <Form.Item name="type" label={<IntlMessage id="status" />} rules={[
                            { 
                                required: true,
                                message: "  "
                            }
                        ]}>
                            <Select>
                                <Option key={0} value="OPEN" ></Option>
                                <Option key={1} value="CLOSED" ></Option>
                            </Select>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Spin>
    );
};

export default RoutineForm